<template>
  <Layout>
    <PageHeader :title="title" :items="items" />


    <div class="row">
      <div class="col-lg-12">
        <div class="card">
          <div class="card-body">
            <div>
            <b-tabs>
              <b-tab active>
                <template #title>
                  <b-spinner type="" small></b-spinner>ATH <strong></strong><b-badge pill variant="danger">{{serviciosAth.length}}</b-badge>
                </template>
                <div class="row">
                  <div class="col-lg-6" v-for="servicio in serviciosAth" :key="servicio.id">
                    <b-card >
                      <b-dropdown right toggle-class="arrow-none card-drop" class="float-right" variant="white">
                          <template v-slot:button-content>
                            <i class="mdi mdi-dots-vertical"></i>
                          </template>
                          <b-dropdown-item @click="rechazar(servicio)">Rechazar</b-dropdown-item>
                          <!-- item-->
                      </b-dropdown>
                      <b-row no-gutters class="align-items-center">
                        <b-col md="3">
                          <b-card-img :src="servicio.cajero_ath.entidad.imagen" class="rounded-0"></b-card-img>
                        </b-col>
                        <b-col md="9">
                          <b-card-body :title="servicio.tipo_llamada+'-'+servicio.llamada">
                            <b-card-text class="m-0">Consecutivo:ATH-{{servicio.id}}</b-card-text>
                            <b-card-text class="m-0">{{servicio.titulo}}</b-card-text>
                            <b-card-text class="m-0">{{servicio.descripcion}}</b-card-text>
                            <b-card-text class="m-0">Terminal: {{servicio.cajero_ath.terminal}}</b-card-text>
                            <b-card-text class="m-0">Direcci√≥n: {{servicio.cajero_ath.direccion}}</b-card-text>
                            <b-card-text class="m-0">Cita: {{servicio.requiere_cita}}</b-card-text>
                            
                            <b-badge variant="success" v-if="servicio.status==='En proceso'">En proceso</b-badge>
                            <b-badge variant="warning" v-if="servicio.status==='Pendiente'">Pendiente</b-badge>
                            <b-badge variant="info" v-if="servicio.status==='Programada'">Programada</b-badge>
                            <b-badge variant="info" v-if="servicio.status==='Reprogramada'">Reprogramada</b-badge>
                            <b-badge variant="success" v-if="servicio.status==='Escalada'">Escalada</b-badge>
                            <b-badge variant="danger" v-if="servicio.status==='Rechazada'">Rechazada</b-badge>
                            <b-badge variant="primary" v-if="servicio.status==='Archivada'">Archivada</b-badge>
                            <b-badge variant="success" v-if="servicio.status==='Cerrada'">Cerrada</b-badge>
                            <b-badge variant="primary" v-if="servicio.status==='Creada'">Creada</b-badge>
                            <b-badge variant="success" v-if="servicio.status==='Aceptada'">Aceptada por analista</b-badge>
                            <b-badge variant="warning" v-if="servicio.status==='Devuelta'">Devuelta</b-badge>
                           <p class="lead text-danger">{{servicio.vencimiento_tecnico | capitalize }}</p>  
                           <a :href="'llamada_ath_tablero/'+servicio.id" style="color:#000">
                            <button
                              type="button"
                              class="btn btn-success btn-block"
                              data-toggle="button"
                              aria-pressed="false"
                            >Gestionar</button></a>
                          </b-card-body>
                        </b-col>
                      </b-row>
                    </b-card>
                </div>
                </div>
              
              </b-tab>

              <b-tab>
                <template #title>
                  <b-spinner type="border" small></b-spinner> Tab 2
                </template>
                <p class="p-3">Tab contents 2</p>
              </b-tab>
            </b-tabs>
          </div>
          </div>
        </div>
      </div>
    </div>
    
    <b-modal id="modal_rechazo" false size="lg"  title="Rechazar" hide-footer>
          <ValidationObserver ref="form"> 
              <b-row>

                <b-col>
                    <div class="form-group">
                    <label>Motivo de rechazo</label>
                    <ValidationProvider name="motivo escalado" rules="required" v-slot="{ errors }">
                            <b-form-textarea
                              id="motivo_rechazo"
                              v-model="form.motivo_rechazo"
                              placeholder="Enter something..."
                              rows="3"
                              max-rows="6"
                              
                            ></b-form-textarea>
                          <span style="color:red">{{ errors[0] }}</span>
                    </ValidationProvider>
              
                    </div>
                </b-col>
              </b-row>
        </ValidationObserver>
        <button class="btn btn-block float-right btn-danger" v-if="form.motivo_rechazo" @click="enviarRechazo()">Enviar</button>
     </b-modal>


  </Layout>
</template>

<script>

import vue2Dropzone from "vue2-dropzone";
import {mapState,mapMutations, mapActions} from 'vuex'
import { ValidationProvider, ValidationObserver } from "vee-validate";
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import moment from 'moment';


/**
 * Dashboard component
 */
export default {
  components: {
    vueDropzone: vue2Dropzone,
    Layout,
    PageHeader,
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      title: "Gestion Tecnica",
      items: [
        {
          text: "Servicios pendiente por cerrar"
        }
      ],
     dropzoneOptions: {
        thumbnailWidth: 150,
        maxFilesize: 0.5,
        headers: { "My-Awesome-Header": "header value" }
      },
      ver:false,
      url:"",
      url_firma:"",
      modal: true,
      file:null,
      firma:null,
      email: "",
      password: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 25, 50, 100],
      filter: null,
      filterOn: [],
      sortBy: "age",
      sortDesc: false,
      fields: ["cargo","descripcion","actions"],
      cargos: [], 
      serviciosAth: [],
      editMode:false,
      form:{
          'id':'',
          'cargo':'',
          'descripcion':'',
          'motivo_rechazo':'',
      }
    }
  },
  computed:{
        ...mapState(['counter'])
   },
  filters: {
        capitalize: function (value) {
          if (!value) return ''
          let fecha = moment();
        
        if (fecha.diff(value, 'hours')>0) {
            return value = -1 * fecha.diff(value, 'hours') + " horas vencido."; 
          }else{
            return value = -1 *fecha.diff(value, 'hours')+ " horas para caducar."; 
          }
        }
    },
  methods: {

    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }, 
    ...mapActions(['guardarUsuario']),
  solicitudAth(data){
      localStorage.setItem("consecutivo", JSON.stringify(data));
      this.$router.push({ path: 'llamada_ath_tablero' })
    },
    switchLoc(){
      if (!this.editMode) {
        this.$refs.form.validate().then(esValido => {
            if (esValido) {
              this.agregarCargo();
            } else {}
          });        
        }else{
          this.$refs.form.validate().then(esValido => {
          if (esValido) {
            this.editarCargos();
          } else {
        }});
      }
    },
      rechazar(data){
        this.form.id=data.id;
        this.form.coordinador_id=data.coordinador_id;
        this.form.motivo_rechazo="";
        this.$root.$emit("bv::show::modal", "modal_rechazo", "#btnShow");
      },
      enviarRechazo(){
        this.$swal({
          title: 'Desea rechazar esta programaci√≥n?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.procesarRechazo();
            
          }
        })
      },
      async procesarRechazo(){
        let data = new FormData();
        data.append('id',this.form.id);
        data.append('coordinador_id',this.form.coordinador_id);
        data.append('motivo_rechazo',this.form.motivo_rechazo);
        await this.axios.post('/api/tecnico/programacion/ath/rechazar',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
              this.$swal(
                    'Rechazado con exito!',
                      '',
                      'success'
                );
                  this.listarServicios();
                     this.$root.$emit("bv::hide::modal", "modal_rechazo", "#btnShow");
                }
              }).catch(e => {

                this.$swal(e.response.data);
          });
      },

   async agregarCargo(){
     let data = new FormData();
      var formulario = this.form;
        for (var key in formulario) {
          data.append(key,formulario[key]);
        }
       await this.axios.post('api/cargos', data, {
           headers: {
            'Content-Type': 'multipart/form-data'
           }}).then(response => {
            if (response.status==200) {
               this.$swal(
                   'Agregado exito!',
                    '',
                    'success');
               this.listarCargos();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
              console.log(e.response.data.menssage);
              this.$swal(e.response.data);
          });
      },
    async editarCargos(){
     let data = new FormData();
       var formulario = this.form;
        for (var key in formulario) {
          data.append(key,formulario[key]);
        }
        await this.axios.put('api/cargos', data).then(response => {
            if (response.status==200) {
               this.$swal('Editado con exito','','success');
               this.listarCargos();
               this.$root.$emit("bv::hide::modal", "modal", "#btnShow");
               ///limpiar el formulario
                for (var key in formulario) {
                   this.form[key]="";
                 }
              }
            }).catch(e => {
                this.$swal('ocurrio un problema','','warning');
            });
     },
     async eliminarCargos(id){
        let data = new FormData();
        data.append('id',id);
        await this.axios.post('api/cargos/delete',data, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }}).then(response => {
              if (response.status==200) {
                this.$swal(
                    'Eliminado con exito!',
                      '',
                      'success'
                );
                this.listarCargos();
                }
              }).catch(e => {
                console.log(e.response.data.menssage);
                this.$swal(e.response.data);
          });
      }, 
      eliminarCargo(id){
        this.$swal({
          title: 'Desea borrar este cargo?',
          icon: 'question',
          iconHtml: '',
          confirmButtonText: 'Si',
          cancelButtonText: 'No',
          showCancelButton: true,
          showCloseButton: true
        }).then((result) => {
          if (result.isConfirmed) {
            this.eliminarCargos(id);
          }
        })
      },
      resete(){
        var formulario = this.form;
        for (var key in formulario) {
             this.form[key]="";
       }
      },
      setear(id) {
        for (let index = 0; index < this.cargos.length; index++) {
          if (this.cargos[index].id===id) {
            this.form.id=this.cargos[index].id;
            this.form.cargo=this.cargos[index].cargo;
            this.form.descripcion=this.cargos[index].descripcion;
            this.$root.$emit("bv::show::modal", "modal", "#btnShow");
            return;
          }
        }
      },
    async  listarServicios(){
       await this.axios.get('api/programacion/ath/servicios')
        .then((response) => {
          this.serviciosAth = response.data;
        })
        .catch((e)=>{
          console.log('error' + e);
        })
      },
      session(){
        if (localStorage.getItem('token')) {
          const token=localStorage.getItem('token');
          this.guardarUsuario(token);
          }else{
          this.$router.push({ name: 'Home' });
          }
        }
  },
    created(){
        this.session();
        this.listarServicios();

      },
     mounted() {

    },
    computed: {
    ...mapState(['usuarioDB']),
    rows() {
      return this.cargos.length;
    },
  },
}
</script>
